


html {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  min-height: 100%;
  position: relative;
  padding-bottom: 3rem;
  background:
    -webkit-linear-gradient(280deg,
      hsla(210, 70%, 24%, 0.8) 20%,
      hsla(210, 70%, 24%, 0.5) 20%,
      hsla(210, 70%, 24%, 0.5) 35%,
      hsla(210, 70%, 20%, 0.6) 35%,
      hsla(210, 70%, 20%, 0.8) 45%,
      hsla(210, 70%, 15%, 0.5) 45%,
      hsla(210, 70%, 15%, 0.8) 75%,
      hsla(210, 70%, 24%, 0.5) 75%
    ),

    -webkit-linear-gradient(45deg,
      hsla(210, 70%, 10%, 0.8) 0%,
      hsla(210, 70%, 20%, 0.8) 50%,
      hsla(210, 70%, 40%, 0.8) 50%,
      hsla(210, 70%, 60%, 0.8) 100%
    )
    hsl(210, 70%, 24%)
  ;
  background:
    -o-linear-gradient(280deg,
      hsla(210, 70%, 24%, 0.8) 20%,
      hsla(210, 70%, 24%, 0.5) 20%,
      hsla(210, 70%, 24%, 0.5) 35%,
      hsla(210, 70%, 20%, 0.6) 35%,
      hsla(210, 70%, 20%, 0.8) 45%,
      hsla(210, 70%, 15%, 0.5) 45%,
      hsla(210, 70%, 15%, 0.8) 75%,
      hsla(210, 70%, 24%, 0.5) 75%
    ),

    -o-linear-gradient(45deg,
      hsla(210, 70%, 10%, 0.8) 0%,
      hsla(210, 70%, 20%, 0.8) 50%,
      hsla(210, 70%, 40%, 0.8) 50%,
      hsla(210, 70%, 60%, 0.8) 100%
    )
    hsl(210, 70%, 24%)
  ;
  background:
    linear-gradient(170deg,
      hsla(210, 70%, 24%, 0.8) 20%,
      hsla(210, 70%, 24%, 0.5) 20%,
      hsla(210, 70%, 24%, 0.5) 35%,
      hsla(210, 70%, 20%, 0.6) 35%,
      hsla(210, 70%, 20%, 0.8) 45%,
      hsla(210, 70%, 15%, 0.5) 45%,
      hsla(210, 70%, 15%, 0.8) 75%,
      hsla(210, 70%, 24%, 0.5) 75%
    ),

    linear-gradient(45deg,
      hsla(210, 70%, 10%, 0.8) 0%,
      hsla(210, 70%, 20%, 0.8) 50%,
      hsla(210, 70%, 40%, 0.8) 50%,
      hsla(210, 70%, 60%, 0.8) 100%
    )
    hsl(210, 70%, 24%)
  ;
}


main {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
  max-width: 800px;
  text-align: left;
  padding: 12px;
  background: rgb(243, 249, 255);
  margin: 20px auto;
  /* border: 2px solid hsl(210, 70%, 25%);
  border-radius: 12px; */
}



  
section {
  border-top: 2px solid hsl(210, 70%, 25%, 0.5);
  padding: 8px;
}

article {
  overflow: auto;
  border-bottom: 1px solid hsla(210, 70%, 25%, 0.25);
  padding: 8px;
}

.ingress {
  font-weight: bold;
}


h1 {font-size: 40px;}
h2 {font-size: 30px;}
h3 {font-size: 20px;}
h4 {font-size: 18px;}
p {
  font-size: 16px;
}

h1, h2, h3, h4 {
  font-weight: bold;
}

i {
  font-style: italic;
}


article img {
  /* float: left; */
  /* width: 100px; */
  margin: 8px;
  margin-right: 16px;
  max-width: 90%;
  /* clear: both; */
  /* border: 2px solid hsl(210, 70%, 25%); */
  border-radius: 12px;
  /* background: white; */
  padding: 4px;
}


.logo {
  float: left;
  width: 100px;
  margin: 8px;
  margin-right: 16px;
  clear: both;
  /* border: 2px solid hsl(210, 70%, 25%); */
  border-radius: 12px;
  /* background: white; */
  padding: 4px;
}


header {
  background: #333;
  color: #fff;
  text-align: center;
  padding: 10px;
}

header h1 {
  margin: 8px;
}

header a {
  color: #fff;
  text-decoration: none;
}

header p {
  margin: 8px;
}





/********* Default css *********/


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



